var debug=false;
export default {
	data() {
		return {
			account:'admin',
			password:'admin'
		}
	},
	methods: {
		login() {
			if(debug)
			{
				uni.switchTab({
					url: '/pages/index/index'
				});
				return;
			}
			this.bus.request({
				url: this.bus.url+'/user/login',
				method: 'POST',
				 header:{
				  'content-type':'application/x-www-form-urlencoded',
				},
				data: {account:this.account,password:this.password},
				success: res => {
					// {
					//   "status": "1",
					//   "data": {
					// 	"userId": "5",
					// 	"account": "admin",
					// 	"nickName": "111",
					// 	"token": "5@eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJleHAiOjE1NjUxNDQ1NDcsImlhdCI6MTU2NTE0MDk0NywidXNlcm5hbWUiOiJhZG1pbiJ9.kg9JmBu65I9rhh0bfynVHCXOtFbpPzDT1eHc9kuh25I",
					// 	"point": null,
					// 	"encryptPwd": "009ae02c2630a3e80fa1cdfb2925d5c7cd745cdd7b2404f2f271705529fdaed5",
					// 	"salt": null,
					// 	"roles": null
					//   },
					//   "errCode": "",
					//   "errMsg": ""
					// }
					if(res.data.status=='1')
					{
						this.bus.login = {account:this.account,password:this.password};
						this.bus.user = res.data.data;
						this.bus.user.header = res.header;
						uni.switchTab({
							url: '/pages/index/index'
						});
					} else{
						uni.showToast({
							title: res.data.errMsg,
							duration: 2000,
							icon:'none'
						});
					}
				},
				fail: () => {
					
				},
				complete: () => {
					
				}
			});
		}
		
	}
}

